// index
.index{
  .title{
    font-size: $fz-title2;
    font-weight: bold;
    text-align: center;
    color: $c-darkRed;
    margin-bottom: 48px;

    @include mobile{
      font-size: $fz-body3;
      margin-bottom: 24px;
    }
    &-italic{
      display: block;
      margin-bottom: 0;
      color: $c-red;
      font-style: italic;

      @include mobile{
        font-size: $fz-title2;
      }
      &:hover{
       color: $c-darkRed; 
      }
    }
  }
  .banner{
    height: 787px;
    text-align: right;
    color: $c-darkRed;
    background-image: url(../images/desktop/home-header.png);
    background-position: center;
    background-size: cover;
  
    @include pad{
      background-image: url(../images/tablet/home-header.png);
    }
    @include mobile{
      height: 396px;
      background-image: url(../images/mobile/home-header.png);
    }
    .container{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
      height: 100%;
    }
    p{
      font-size: $fz-body1;
  
      @include mobile{
        font-size: $fz-body5;
      }
    }
    h2{
      font-size: $fz-title1;
      font-weight: bold;
      margin-bottom: 4px;
  
      @include mobile{
        font-size: $fz-body3;
        margin-bottom: 8px;
      }
    }
    a{
      display: inline-block;
      padding: 6px 12px;
      font-size: $fz-body4;
      font-weight: 500;
      color: $c-white;
      background-color: $c-red;
  
      &:hover{
        background-color: $c-darkRed;
      }
    }
  }
}

// features
.features{
  background-color: $c-rice;

  .title{
    margin-bottom: 32px;
  }
  ul{
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }
  li{
    width: calc((100% - 72px) / 4);

    @include pad{
      width: calc((100% - 24px) / 2);
    }
    @include mobile{
      width: 100%;
    }
  }
  h3{
    margin-bottom: 8px;
    text-align: center;
    font-size: $fz-body3;
    font-weight: bold;
    color: $c-darkRed;
  }
  img{
    width: 83px;
    margin: 0 auto 24px;
  }
}

// series
.series{
  img{
    margin-bottom: 8px;

    @include pad {
      width: 50%;
      margin: 0 auto 8px;
    }

    @include mobile{
      width: 100%;
    }
  }
  ul{
    display: flex;
    gap: 24px;

    @include pad {
      flex-direction: column;
      align-items: center;
    }
  }
}

// coBranding
.coBranding{
  background-image: url(../images/desktop/background-desktop.png);
  background-position: center;
  background-size: cover;

  @include pad{
    background-image: url(../images/tablet/background-tablet.png);
  }
  @include mobile{
    background-image: url(../images/mobile/background-phone.png);
  }
  ul{
    display: flex;

    @include pad{
      flex-direction: column;
      gap: 24px;
    }
  }
  a{
    background-color: $c-white;
    padding: 8px 0;
  }
}

// recommend
.recommend{
  ul{
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }
  li{
    width: calc((100% - 72px) / 4);
    box-shadow: 0px 2px 6px #00000029;
    border-radius: 4px;
  
    @include pad{
      width: calc((100% - 24px) / 2);
    }
    @include mobile{
      width: 100%;
    }
  }
  h3{
    margin-bottom: 8px;
    font-weight: 500;
  }
  &-text{
    padding: 16px;
    
    p{
      margin-bottom: 72px;
    }
    time{
      color: $c-gray;
    }
  }
}

// contact
.contact{
  background-color: $c-rice;

  .section{
    max-width: 50%;
    margin: 0 auto;

    @include pad{
      max-width: 75%;
    }
    @include mobile{
      max-width: 100%;
    }
  }
  p{
    font-size: $fz-body4;
    margin-bottom: 24px;

    @include mobile{
      font-size: $fz-body5;
    }
  }
  label{
    display: inline-block;
    margin-bottom: 8px;
  }
  .input{
    width: 100%;
    margin-bottom: 16px;
    padding: 6px 12px;
    font-size: $fz-body5;
    line-height: 1.5;
    border: 1px solid $c-black;
    border-radius: 0;
    background-color: $c-rice;
  }
  textarea{
    height: 136px;
    resize: none;
  }
  .checkbox{
    width: 24px;
    height: 24px;
    cursor: pointer;
  }
  .line{
    display: flex;
    align-items: center;
    margin-bottom: 48px;

    @include mobile{
      margin-bottom: 24px;
    }
    label{
      margin-bottom: 0;
      margin-left: 4px;
    }
  }
  button{
    display: block;
    width: 66.6%;
    margin: 0 auto;
    padding: 16px 0;
    font-size: $fz-body5;
    font-weight: bold;
    line-height: 1.5;
    border: none;
    color: $c-white;
    background-color: $c-red;
    cursor: pointer;
    transition: all 0.3s;
  
    &:hover{
      background-color: $c-darkRed;
    }

    @include pad{
      width: 60%;
    }
    @include mobile{
      width: 100%;
    }
  }
}