// store
.store{
  label{
    font-size: $fz-body3;

    @include mobile{
      font-size: $fz-body5;
    }
  }
  select{
    width: 304px;
    margin-left: 16px;
    padding: 6px 12px;
    border: 1px solid #CED4DA;
    font-size: 18px;

    @include mobile{
      width: 276px;
      margin-left: 8px;
    }
  }
  .select{
    display: flex;
    align-items: center;
    margin-bottom: 40px;

    @include mobile{
      margin-bottom: 24px;
    }
  }
  &-list{
    display: flex;
    flex-wrap: wrap;
    row-gap: 48px;
    column-gap: 24px;

    @include pad{
      row-gap: 24px;
    }
  }
  &-card{
    width: calc((100% - 48px) / 3);
    @include pad{
      width: calc((100% - 24px) / 2);
    }
    @include mobile{
      width: 100%;
    }
    .mdi{
      font-size: 24px;
      line-height: 24px;
      margin-top: 2px;
      margin-right: 8px;
    }
    ul{
      border: 1px solid #00000020;
      padding: 16px 24px;
      font-size: $fz-body4;
    }
    li{
      display: flex;
      margin-bottom: 8px;
    }
    li:last-child{
      margin-bottom: 0;
    }
  }
  h3{
    border: 1px solid #00000020;
    padding: 8px 24px;
    font-size: $fz-body3;
  }
  a{
    display: block;
    padding: 16px 0;
    font-size: $fz-body4;
    text-align: center;
    color: $c-white;
    background-color: $c-black;

    &:hover{
      background-color: $c-darkRed;
    }
  }
}

// store-detail
.store-detail{
  .store-content{
    display: flex;
    
    @include pad{
      flex-direction: column;
    }
  }
  .store-card{
    width: 42%;
    margin-right: 24px;

    @include pad{
      width: 100%;
      margin-right: 0;
      display: flex;
      gap: 24px;
      margin-bottom: 24px;

      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      div{
        width: 100%;
      }
    }
    @include mobile{
      display: block;
    }
  }
  .store-map{
    width: 58%;
    background-image: url(../images/desktop/storepage-2.png);
    background-size: cover;
    background-position: center;

    @include pad{
      width: 100%;
      height: 414px;
      background-image: url(../images/tablet/storepage-2.png);
    }
    @include mobile{
      height: 208px;
      background-image: url(../images/mobile/storepage-2.png);
    }
  }
}